.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.react {
  background-color: yellow;
  color: red;
  font-size: 48px;
  font-weight: bold;
  padding: 16px;
}
.modal {
  padding: 20px;
  background-color: rgba(166, 143, 12, 0.6);
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
.bgTitle {
  display: flex;
  background-color: skyblue;
  color: #fff;
  justify-content: center;
}
div {
  box-sizing: border-box;
}
.list {
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid #333;
}
.topBt {
  margin-top: 20px;
  float: right;
  padding-right: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
